body{
    overflow-x: hidden;
}

section{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: #fff;
    min-height: 100vh;
    padding: 200px 0;

    .text-container{
        width: 100%;
        max-width: 620px;
        margin: 0 auto;
        h3{
            font-family: Arial;
            font-size: 30px;
            font-weight: 700;
            margin: 0 0 30px 0;
        }
        p, a{
            font-family: Arial;
            font-size: 18px;
            line-height: 30px;
            margin: 0 0 10px 0;
        }
        a{
            text-decoration: underline;                
            color: #c7303c;
        }
    }

    .logo-pattern{
        position: absolute;
        bottom: 0;
        left: 0;
        width: 130%;
        height: 90px;
        background: url("../img//svg-icons/logo.svg");
        opacity: 0.2;
    }
    
    .img-container{
        &.medium{
            width: 60%;
            margin: 100px auto 0;
            img{
                width: 100%;
                height: auto;
            }
        }
        &.large{
            width: 100%;
            img{
                width: 100%;
                height: auto;
            }
        }
    }

    .colors-container{
        display: flex;
        justify-content: center;
        .circle{
            display: flex;
            justify-content: center;
            align-items: center;
            width: 200px;
            height: 200px;
            border-radius: 50%;
            margin: 100px 50px;
            &.red{
                transform: translate(300px, 0);
                // background: #C7303C;
            }
            &.black{
                // background: #2C2B2A;
            }
            &.gray{
                transform: translate(-300px, 0);
                // background: #E1E6E9;
            }
            span{
                font-family: Arial, Helvetica, sans-serif;
                font-weight: bold;
                color: #fff;
                opacity: 0;
                transform: translate(0, 50px);
            }
        }
    }

}

.first-block{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    width: 100%;
    background: url("../img/book.png") no-repeat;
    background-size: cover;
    .img-container{
        width: 415px;
        height: 80px;
        padding: 11px 17px;
        background: #2c2b2a;
        z-index: 2;
    }
    .book-cont{
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 1;
        img{
            width: 100%;
            height: 100%;
            transform: scale(1.8);
            animation: anim 4s infinite;
        }
        @keyframes anim {
            0%{
                transform: translate(0,0) scale(1.8);
            }
            50%{
                transform: translate(0,1.5%) scale(1.8);
            }
            100%{
                transform: translate(0,0) scale(1.8);
            }
        }
    }
}

.result-section{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: #fff;
    min-height: 100vh;
    .block{
        max-width: 100%;
        width: 100%;
        margin: 20px auto;
        padding: 100px;
        color: #fff;
        &.black{
            background: #2c2b2a;
            transform: translate(-400%, 0);
        }
        &.red{
            background: #c7303c;
            transform: translate(400%, 0);
        }
        .text-container{
            opacity: 0;
            transform: translate(0, 100px);
        }
    }
}

/*** PROTOTYPES ANIMATION ***/

.prototypes-animation{
    background: #2c2b2a;
    overflow: hidden;
    .animation-container{
        max-width: 1200px;
        margin: 0 auto;
        .row{
            display: flex;
            flex-wrap: wrap;
            .col{
                width: 33%;
                padding: 0 20px;
                img{
                    width: 100%;
                    margin: 20px 0;
                }
            }
            
            &:nth-of-type(1){
                .col{
                    &:nth-of-type(1){
                        img{
                            &:nth-of-type(1){
                                transform: translate(65%, 0);
                            }
                            &:nth-of-type(2){
                                transform: translate(111%, 50%);
                            }
                        }
                    }
                    &:nth-of-type(2){
                        z-index: 2;
                    }
                    &:nth-of-type(3){
                        img{
                            &:nth-of-type(1){
                                transform: translate(-65%, 0);
                            }
                            &:nth-of-type(2){
                                transform: translate(-111%, -40%);
                            }
                        }
                    }
                }
            }

            &:nth-of-type(2){
                .col{
                    &:nth-of-type(1){
                        img{
                            &:nth-of-type(1){
                                transform: translate(111%, 30%);
                            }
                            &:nth-of-type(2){
                                transform: translate(111%, 0);
                            }
                        }
                    }
                    &:nth-of-type(2){
                        z-index: 2;
                        img{
                            &:nth-of-type(1){
                                transform: translate(0, 80%);
                            }
                        }
                    }
                    &:nth-of-type(3){
                        img{
                            &:nth-of-type(1){
                                transform: translate(-111%, 60%);
                            }
                            &:nth-of-type(2){
                                transform: translate(-111%, 0);
                            }
                        }
                    }
                }
            }

        }
    }
}

/*** PROTOTYPES ANIMATION ***/
.gadjet-animation{
    height: 400vh;
    margin: 200px 0 0 0;
    padding: 0;
    justify-content: flex-start;
    .img-container{
        position: relative;
        width: 80%;
        height: 100vh;
        overflow: hidden;
        margin: 0 auto;
        &:nth-of-type(2){
            height: 300vh;
            width: 100%;
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
        }
        img{
            object-fit: cover;
            position: absolute;
            width: auto;
            height: auto;
            min-width: 100%;
            min-height: 100%;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }
}

/*** MOCKUP ANIMATION ***/

.mockup-animation{
    padding: 0;
    &.fixed{
        .animation-container{
            .col{
                .desktop-mockup{
                    position: fixed;
                    top: 0;
                    right: 0;
                    width: 60%;
                }
                .phone-mockup{
                    position: fixed;
                    top: 0;
                    left: 0;
                    width: 40%;
                }
            }
        }
    }
    &.fixed-bottom{
        .animation-container{
            .col{
                .desktop-mockup{
                    position: absolute;
                    bottom: 0;
                    top: auto;
                    width: 100%;
                }
                .phone-mockup{
                    position: absolute;
                    bottom: 0;
                    top: auto;
                    width: 100%;
                }
            }
        }
    }
    .animation-container{
        display: flex;
        .col{
            position: relative;
            display: flex;
            flex-direction: column;
            justify-content: start;
            &.col-phone-mockup{
                width: 40%; 
                .feed-img{
                    padding: calc((100vh - (50vw / 2)) / 2) 2% calc((100vh - (50vw / 1.8)) / 2) 2%;
                    width: 15vw;
                    margin: 0 auto;
                    position: sticky;
                    top: 0;
                    // img{
                    //     position: sticky;
                    //     top: calc((100vh - (50vw / 2)) / 2);
                    //     width: 100%;
                    // }
                }
            }
            &.col-desktop-mockup{
                width: 60%;
                .feed-img{
                    width: 37vw;
                    padding: calc((100vh - (50vw / 2)) / 2)  0  calc((100vh - (50vw / 1.8)) / 2)  0;
                    margin: 0 auto;
                    position: sticky;
                    top: 0;
                    // img{
                    //     position: sticky;
                    //     top: calc((100vh - (50vw / 2)) / 2);
                    //     width: 100%;
                    // }
                }
            }

            .phone-mockup{
                position: absolute;
                top: 0;
                left: 0;
                height: 100vh;
                width: 100%;
                z-index: 1;

                &.fixed{
                    position: fixed;
                    top: 0;
                    left: 0;
                    width: 40%;
                }
                &.fixed-bottom{
                    position: absolute;
                    bottom: 0;
                    top: auto;
                    width: 100%;
                }

                .top-cap,
                .bottom-cap{
                    position: absolute;
                    left: 0;
                    background: #fff;
                    height: calc((100vh - (50vw / 1.8)) / 2);
                    width: 100%;
                    z-index: -1;
                }
                .top-cap{
                    top: 0;
                }
                .bottom-cap{
                    bottom: 0;
                }

                .mockup-img{
                    position: absolute;
                    height: calc( 50vw / 1.7);
                    width: 15vw;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    z-index: 2;
                }

            }

            .desktop-mockup{
                position: absolute;
                top: 0;
                right: 0;
                height: 100vh;
                width: 100%;
                z-index: 1;

                &.fixed{
                    position: fixed;
                    top: 0;
                    right: 0;
                    width: 60%;
                }
                &.fixed-bottom{
                    position: absolute;
                    bottom: 0;
                    top: auto;
                    width: 100%;
                }

                .top-cap,
                .bottom-cap{
                    position: absolute;
                    left: 0;
                    background: #fff;
                    height: calc((100vh - (50vw / 1.8)) / 2);
                    width: 100%;
                    z-index: -1;
                }
                .top-cap{
                    top: 0;
                }
                .bottom-cap{
                    bottom: 0;
                }

                .mockup-img{
                    position: absolute;
                    height: calc( 50vw / 1.7 );
                    width: 50vw;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    z-index: 2;
                }
            }

        }
    }
}